<template>
  <div>
    <vue-html2pdf
      :show-layout="false"
      :float-layout="true"
      :enable-download="true"
      :preview-modal="false"
      :filename="`ParniaMaroofi-${lang}`"
      :pdf-quality="2"
      :manual-pagination="true"
      pdf-format="a3"
      pdf-orientation="portrait"
      pdf-content-width="100%"
      :paginate-elements-by-height="1000"
      @hasDownloaded="hasDownloaded($event)"
      ref="html2Pdf"
    >
      <section slot="pdf-content">
        <div class="pdf-header" style="height: 200px; background-color: #455a64; postion: relative">
          <div
            style="position: absolute; top: 27px"
            :style="isLtr ? 'right: 90px' : 'right: 470px; text-align: right'"
            :dir="isLtr ? 'ltr' : 'rtl'"
          >
            <p
              style="color: #fff"
              :style="
                isLtr
                  ? 'font-size: 70px; margin-bottom: 0; transform: translateX(-3px)'
                  : 'font-size: 45px; margin-bottom: 10px'
              "
            >
              {{ terms.name }}
            </p>
            <p
              class="mb-1"
              style="font-size: 27px; color: #fff; transform: translateY(-12px)"
              :style="isLtr ? 'font-size: 27px' : 'font-size: 22px;'"
            >
              {{ terms.jobTitle }}
            </p>
          </div>
        </div>
        <div style="display: flex" :dir="isLtr ? 'ltr' : 'rtl'">
          <div
            style="
              background-color: #dedede;
              width: 400px;
              min-width: 400px;
              padding: 160px 30px 70px;
              position: relative;
              text-align: left;
            "
          >
            <div style="position: absolute; top: -130px; left: 50%; transform: translateX(-50%)">
              <img
                class="avatar-image"
                style="width: 260px; height: 260px; border-radius: 50%; border: 15px solid #dedede"
                src="/parnia.jpg"
              />
            </div>
            <!-- -------------------------- Contact -------------------------------- -->
            <p
              style="font-size: 25px; margin-bottom: 25px"
              :style="!isLtr ? 'margin-top: -10px' : ''"
            >
              {{ terms.contact }}
            </p>

            <p
              style="font-size: 1rem; color: #4a4a4a; margin-bottom: 12px"
              dir="ltr"
              :class="!isLtr ? 'persian-font' : ''"
            >
              <v-icon style="margin-right: 5px">mdi-phone-in-talk-outline</v-icon>
              (+98) 9226907483
            </p>

            <p style="font-size: 1rem; color: #4a4a4a; margin-bottom: 12px">
              <v-icon style="margin-right: 5px">mdi-web</v-icon>
              parniamaroofi.github.io
            </p>

            <p style="font-size: 1rem; margin-bottom: 12px; color: #4a4a4a">
              <v-icon style="margin-right: 5px">mdi-gmail</v-icon>
              parniamaroofi@gmail.com
            </p>

            <p style="font-size: 1rem; color: #4a4a4a; margin-bottom: 12px">
              <v-icon style="margin-right: 5px">mdi-linkedin</v-icon>
              www.linkedin.com/in/parniamaroofi
            </p>

            <!-- -------------------------- About Me -------------------------------- -->
            <div style="padding: 60px 0">
              <p style="font-size: 25px; margin-bottom: 20px; text-transform: uppercase">
                {{ terms.aboutMe }}
              </p>
              <p style="font-size: 0.98rem; text-align: justify">{{ aboutMe[lang] }}</p>
            </div>

            <!-- -------------------------- Languages -------------------------------- -->
            <p style="font-size: 25px; margin-bottom: 30px; text-transform: uppercase">
              {{ terms.languages }}
            </p>
            <v-row>
              <v-col
                v-for="(item, index) in languages"
                :key="`languages${index}`"
                style="text-align: center"
                cols="6"
              >
                <!-- language progress -->
                <v-progress-circular
                  :rotate="360"
                  :size="90"
                  :width="8"
                  :value="item.progress"
                  color="#455A64"
                >
                  <!-- language progress value -->
                  <span style="font-size: 1rem" :class="!isLtr ? 'persian-font' : ''"
                    >{{ item.progress }}%</span
                  >
                </v-progress-circular>
                <!-- Language title -->
                <p style="font-size: 1rem; margin-top: 10px; margin-bottom: 0; color: #4a4a4a">
                  {{ item[lang + 'name'] }}
                </p>
                <p style="font-size: 0.8rem; margin-bottom: 0" v-if="item.ENname == 'Persian'">
                  ({{ terms.native }})
                </p>
              </v-col>
            </v-row>

            <!-- <p
              style="
                font-size: 25px;
                margin-top: 80px;
                margin-bottom: 15px;
                text-transform: uppercase;
              "
              :style="isLtr ? 'margin-top: 90px;' : 'margin-top: 77px;'"
            >
              {{ terms.softSkills }}
            </p> -->
            <!-- -------------------------- Soft Skills -------------------------------- -->
            <!-- <div>
              <p
                v-for="(item, index) in softSkills"
                :key="`softSkills${index}`"
                :style="
                  index != softSkills.length - 1 ? 'margin-bottom: 17px' : 'margin-bottom: 0px'
                "
                class="fs-xxlarge"
              >
                <v-icon :style="isLtr ? 'margin-right: 5px' : 'margin-left: 5px'" color="#455A64"
                  >mdi-checkbox-marked-outline</v-icon
                >{{ item[lang + 'name'] }}
              </p>
            </div> -->
          </div>
          <div style="background-color: #fff; width: 100%; padding: 40px">
            <!-- -------------------------- Educations -------------------------------- -->
            <p style="font-size: 25px; margin-bottom: 15px; text-transform: uppercase">
              {{ terms.educations }}
            </p>
            <div v-for="(item, index) in educations" :key="`educations${index}`">
              <div style="display: flex; justify-content: space-between">
                <p style="font-size: 1.2rem; margin-bottom: 3px">
                  {{ item[lang + 'name'] }}
                </p>
                <p
                  style="font-size: 0.9rem; width: 350px"
                  :style="isLtr ? 'text-align: right' : 'text-align: left'"
                  :class="!isLtr ? 'persian-font' : ''"
                >
                  {{ item[lang + 'duration'] }}
                </p>
              </div>
              <p style="font-size: 1rem; color: #777; margin-bottom: 2px">
                {{ item[lang + 'degree'] }}
              </p>
              <p style="font-size: 1rem; color: #777">{{ item[lang + 'location'] }}</p>
            </div>
            <!-- -------------------------- Experiences -------------------------------- -->
            <p
              style="
                font-size: 25px;
                margin-top: 60px;
                margin-bottom: 15px;
                text-transform: uppercase;
              "
            >
              {{ terms.experiences }}
            </p>
            <div
              v-for="(item, index) in experiences"
              :key="`experiences${index}`"
              style="margin-bottom: 40px"
            >
              <div style="display: flex; justify-content: space-between">
                <div style="display: flex; align-items: center">
                  <img
                    :src="item.logo"
                    width="60"
                    height="60"
                    style="border-radius: 8px; margin-top: 4px; margin-bottom: 15px"
                    :style="isLtr ? 'margin-right: 15px' : 'margin-left: 15px'"
                  />

                  <div>
                    <p style="font-size: 1.2rem; margin-bottom: 3px; white-space: nowrap">
                      {{ item[lang + 'name'] }}
                    </p>

                    <p style="font-size: 1rem; color: #777">{{ item[lang + 'location'] }}</p>
                  </div>
                </div>
                <p
                  style="font-size: 0.9rem; width: 350px; margin-top: 5px"
                  :style="isLtr ? 'text-align: right' : 'text-align: left'"
                  :class="!isLtr ? 'persian-font' : ''"
                >
                  {{ item[lang + 'duration'] }}
                </p>
              </div>

              <p
                style="font-size: 1rem; color: #777; margin-top: 5px"
                :style="
                  isLtr
                    ? 'padding-left: 10px; border-left: 3px solid #406882'
                    : 'padding-right: 10px; border-right: 3px solid #406882'
                "
              >
                {{ item[lang + 'position'] }}
              </p>
            </div>

            <p
              style="
                font-size: 25px;
                margin-top: 50px;
                margin-bottom: 40px;
                text-transform: uppercase;
              "
              :style="isLtr ? 'margin-top: 70px;' : 'margin-top: 60px;'"
            >
              {{ terms.skills }}
            </p>
            <!-- -------------------------- Skills -------------------------------- -->
            <div>
              <v-row>
                <v-col
                  v-for="(item, index) in skills"
                  :key="`skills${index}`"
                  cols="6"
                  style="padding: 7px 10px !important"
                >
                  <div style="display: flex; justify-content: start">
                    <v-icon>mdi-circle-medium</v-icon>
                    <span style="font-size: 1rem">{{ item.name }}</span>
                    <span v-if="item.familiar" class="px-3">
                      <v-chip outlined color="secondary" style="height: 20px">
                        <span class="fs-xxsmall">{{ terms.familiar }}</span>
                      </v-chip>
                    </span>
                  </div>
                </v-col>
              </v-row>
            </div>
          </div>
        </div>
      </section>
    </vue-html2pdf>
  </div>
</template>

<script>
import VueHtml2pdf from 'vue-html2pdf';
import { EventBus } from './../event-bus';
import utilities from './../mixin/utilities';
export default {
  components: {
    VueHtml2pdf,
  },
  mixins: [utilities],
  data() {
    return {};
  },
  methods: {
    generateReport() {
      EventBus.$emit('download-event', true);

      setTimeout(() => {
        this.$refs.html2Pdf.generatePdf();
      }, 200);
    },
    hasDownloaded() {
      EventBus.$emit('download-event', false);
    },
  },
  mounted() {
    const thisInstance = this;
    this.$root.$on('generateReportEvent', function () {
      thisInstance.generateReport();
    });
  },
};
</script>

<style lang="scss">
.avatar-image {
  position: relative;
  &::before {
    content: '';
    width: 150px;
    height: 150px;
    top: -10px;
    right: -50px;
    position: absolute;
    background-color: red;
    z-index: 1000;
  }
}
</style>
